body {
    margin: 0;
    overflow: auto;
    overflow-x: hidden;
    height: 300vh;
    width: 100vw;
}

main {
    height: 100vh;
    width: 100vw;
    max-width: 99vw;
    margin: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    gap: 5vw;
    background: #0b192f;
    color: #ccd6f6;
    overflow: hidden;
}

button{
    height:6vh;
    background: rgb(239, 109, 35);
    font-size: 3vh;
}

button:hover{
    background: rgb(28, 183, 215);
    transition: 0.3s;
}

h1 {
    font-size: 5vw;
    margin: 0;
}

p {
    font-size: 2.5vw;
    margin:0;
}

h3{
    font-size: 1.5vw;
}

video {
    height:70vh;
    width:auto;
    z-index: 0;
    border-radius:.5vw;
}

form {
    display: flex;
    flex-flow: column nowrap;
    width: 30vw;
    font-size: 1.5vw;
    background:white;
    border-radius: .5vw;
    padding:3vh;
    color:black;
}

input {
    height: 4vh;
    font-size: 2.6vh;
    padding:1vh;
}

label{
    margin-top:2vh;
}

textarea {
    height: 15vh;
    font-size:2.6vh;
    padding:1vh;
    margin-bottom: 2vh;
}


.carousel {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    gap:2vh;
}

.central {
    display: flex;
    flex-flow: column nowrap;
    justify-content: start;
    align-items: start;
    width: 45vw;
}



.icon {
    z-index: 1;
    color: blue;
    height: 80vh;
}

.blue {
    color: rgb(28, 183, 215);
    text-shadow: 1px 1px rgb(239, 109, 35);
    /*     color: rgb(239,109,35);
    text-shadow: 1px 1px rgb(28,183,215); */
}

.orange {
    color: rgb(239, 109, 35);
    /*     color: rgb(28,183,215);
    text-shadow: 1px 1px rgb(239,109,35); */
}

.side {
    width: 30vw;
    height: 100vh;
    display:flex;
    justify-content: center;
    align-items: center;
}

.video-viewer {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
}

.caption {
    z-index: 1;
    border-radius: 1vh;
    width: 100%;
}


.video-container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.flex-row {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    align-items: start;
    gap: 5vw;
}

#root {
    margin: 0;
}